.app__about {
  flex: 1;
  width: 100%;
  flex-direction: column;
}

.app__profiles {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 2rem;
}

.app__profiles-item {
  width: 190px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  margin: 2rem;
  img {
    width: 100%;
    height: 170px;
    border-radius: 15px;
    position: cover;
  }
  @media screen and (min-width: 2000px) {
      width: 100%;
      margin: 2rem 4rem;
      img{
          height: 320px;
      }
  }
}
